function ChartPage() {
  // DB     
  // const [ChartData, setChartData] = useState(ChartSetting)
  // useEffect(() => {
  //   axios.get('http://localhost:3001/chart/test')
  //     .then(res => setChartData(res.data))
  //     .catch(function (error) {
  //       console.log(error);
  //     })
  // })
  const [ChartData, setChartData] = useState(ChartData_1)

  return (
    <Container>
      <div>
        <Button type="primary" style={{ marginRight: 20 }} onClick={() =>setChartData(ChartData_1)}>   </Button>
        <Button type="primary" style={{ marginRight: 20 }} onClick={() =>setChartData(ChartData_2)}>  3 </Button>
        <Button type="primary" style={{ marginRight: 20 }} onClick={() =>setChartData(ChartData_3)}>   </Button>
        <Button type="primary" style={{ marginRight: 20 }} onClick={() =>setChartData(ChartData_4)}> se 44mm </Button>
        <Button type="primary" style={{ marginRight: 20 }} onClick={() =>setChartData(ChartData_5)}>  G102 </Button>
      </div>

      <h1> {ChartData.id} </h1>
      <Line
        width={900}
        height={500}
        data={ChartData} // Data  
        margin={{ top: 50, right: 110, bottom: 50, left: 60 }}
        xScale={{ type: 'point' }}
        yScale={{ type: 'linear', stacked: true, min: 'auto', max: 'auto' }}
        axisTop={null}
        axisRight={null}
        axisBottom={{
          orient: 'bottom',
          tickSize: 5,
          tickPadding: 5,
          tickRotation: 0,
          legend: '',
          legendOffset: 36,
          legendPosition: 'middle',
        }}
        axisLeft={{
          orient: 'left',
          tickSize: 2,
          tickPadding: 5,
          tickRotation: 0,
          legend: '',
          legendOffset: -50,
          legendPosition: 'middle',
        }}
        colors={{ scheme: 'accent' }}
        lineWidth={4} // 
        pointSize={10}
        pointColor="white"
        pointBorderWidth={2}
        pointBorderColor={{ from: 'serieColor' }}
        pointLabel="y"
        pointLabelYOffset={-12}
        useMesh={true}
      />
    </Container>
  );
}

export default ChartPage

const Container = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items:center;
  margin-top:30px;
`